
import React from "react";
import PropTypes from "prop-types";

import { scaleTime } from "d3-scale";
import { utcHour } from "d3-time";
import { format } from "d3-format";
import { OHLCTooltip } from "react-stockcharts/lib/tooltip";
import { timeFormat } from "d3-time-format";
import {
	CrossHairCursor,
	EdgeIndicator,
	CurrentCoordinate,
	MouseCoordinateX,
	MouseCoordinateY,
} from "react-stockcharts/lib/coordinates";
import { SvgPathAnnotation, Label, Annotate } from "react-stockcharts/lib/annotation";
import { ChartCanvas, Chart, ZoomButtons } from "react-stockcharts";
import { CandlestickSeries } from "react-stockcharts/lib/series";
import { XAxis, YAxis } from "react-stockcharts/lib/axes";
import { fitWidth } from "react-stockcharts/lib/helper";
import { last, timeIntervalBarWidth } from "react-stockcharts/lib/utils";
import LabelAnnotation from "react-stockcharts/lib/annotation/LabelAnnotation";
import dayjs from "dayjs";

class CandleStickChart extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			// candlesAppearance: {
			// 	wickStroke: "#000",
			// 	fill: function fill(d) {
			// 	  return d.close > d.open ? "#6BA583" : "#FF0000";
			// 	},
			// 	stroke: "#000",
			// 	candleStrokeWidth: 10,
			// 	widthRatio: 0.8,
			// 	opacity: 1,
			// 	width: 4
			// }
			buyBarAnnotationProps: {
				fontSize: 25,
				fill: "#6BA583",
				opacity: 1,
				text: "―",
				y: ({ yScale }) => yScale.range()[0] - 200,
				onClick: console.log.bind(console),
				tooltip: d => timeFormat("%B")(d.date),
			},
			sellBarSellAnnotationProps: {
				fontSize: 25,
				fill: "#FF0000",
				opacity: 1,
				text: "―",
				y: ({ yScale }) => yScale.range()[0] - 80,
				onClick: console.log.bind(console),
				tooltip: d => timeFormat("%B")(d.date),
			},
			buyAnnotationProps: {
				fontSize: 16,
				fill: "#6BA583",
				opacity: 1,
				text: "▲",
				y: ({ yScale }) => yScale.range()[0] - 50,
				onClick: console.log.bind(console),
				tooltip: d => timeFormat("%B")(d.date),
				// onMouseOver: console.log.bind(console),
			},
			sellAnnotationProps: {
				fontSize: 16,
				// rotate: 90,
				fill: "#FF0000",
				opacity: 1,
				text: "▼",
				y: ({ yScale }) => (yScale.range()[0]/3) - 50,
				onClick: console.log.bind(console),
				tooltip: d => timeFormat("%B")(d.date),
				// onMouseOver: console.log.bind(console),
			},
			closedAnnotationProps: {
				fontSize: 10,
				// rotate: 90,
				fill: "#333",
				opacity: 1,
				text: "Market Closed",
				y: ({ yScale }) => yScale.range()[0]/2,
				onClick: console.log.bind(console),
				tooltip: d => timeFormat("%B")(d.date),
				// onMouseOver: console.log.bind(console),
			}
		}
		this.handleReset = this.handleReset.bind(this);
	}


	componentWillMount(){
		this.setState({
			suffix: 1
		});

	}

	handleReset() {
		this.setState({
			suffix: this.state.suffix + 1
		});
	}
	
	render() {
		const { type, width, height, data, ratio } = this.props;
		let num = 100;
		if(height < 300){
			num = 30;
		}
		const xAccessor = d => d.time;
		const xExtents = [
			xAccessor(last(data)),
			xAccessor(data[data.length - num])
		];
		
		return (
			<ChartCanvas height={height}
					ratio={ratio}
					width={width}
					margin={{ left: 50, right: 50, top: 10, bottom: 30 }}
					type={type}
					seriesName={`MSFT_${this.state.suffix}`}
					data={data}
					xAccessor={xAccessor}
					xScale={scaleTime()}
					xExtents={xExtents}
					yExtents={[d => d.volume]}
					>

				<Chart id={1} yExtents={d => [d.high, d.low]}>
					<XAxis axisAt="bottom" orient="bottom" ticks={6}/>
					<YAxis axisAt="right" orient="right" ticks={5} tickFormat={format(".6f")} />
					<ZoomButtons onReset={this.handleReset}/>
					{/* <CandlestickSeries/> */}
					<MouseCoordinateX
						rectWidth={60}
						at="bottom"
						orient="bottom"
						displayFormat={timeFormat("%d %b %Y %H:%M:%S")} />
					<MouseCoordinateY
						at="right"
						orient="right"
						displayFormat={format(".4f")} />

					<CandlestickSeries width={6}/>

					<EdgeIndicator itemType="last" orient="right" edgeAt="right"
						yAccessor={d => d.close} fill={d => d.close > d.open ? "#6BA583" : "#FF0000"}/>

					<OHLCTooltip ohlcFormat={format(".5f")} origin={[-40, 0]}/>

					<Annotate with={LabelAnnotation}
						when={d => +d.buy === 0.99 /* some condition */}
						usingProps={this.state.buyAnnotationProps} />
					
					<Annotate with={LabelAnnotation}
						when={d => d.sell_take_profit != 'nan' && d.sell_take_profit  /* some condition */}
						usingProps={this.state.sellBarSellAnnotationProps} />

					<Annotate with={LabelAnnotation}
						when={d => d.buy_take_profit != 'nan' && d.buy_take_profit}
						usingProps={this.state.buyBarAnnotationProps} />
					
					<Annotate with={LabelAnnotation}
						when={d => d.buy_take_profit != 'nan' && d.buy_take_profit}
						usingProps={this.state.sellBarSellAnnotationProps} />
					
					<Annotate with={LabelAnnotation}
						when={d => d.sell_take_profit != 'nan' && d.sell_take_profit  /* some condition */}
						usingProps={this.state.buyBarAnnotationProps} />
					
					<Annotate with={LabelAnnotation}
						when={d => dayjs(d.time * 1000).format("d H") == '0 0'}
						usingProps={this.state.closedAnnotationProps} />
					
					<Annotate with={LabelAnnotation}
						when={d => +d.sell === 0.01 /* some condition */}
						usingProps={this.state.sellAnnotationProps} />
				</Chart>
				<CrossHairCursor />
			</ChartCanvas>
		);
	}
}

CandleStickChart.propTypes = {
	data: PropTypes.array.isRequired,
	width: PropTypes.number.isRequired,
	ratio: PropTypes.number.isRequired,
	type: PropTypes.oneOf(["svg", "hybrid"]).isRequired,
};

CandleStickChart.defaultProps = {
	type: "svg",
};
CandleStickChart = fitWidth(CandleStickChart);

export default CandleStickChart;
