
import React from 'react';
import { render } from 'react-dom';
import { TypeChooser } from "react-stockcharts/lib/helper";
import { API_URL, SOCKET_URL } from '../../config';
import io from 'socket.io-client';
import upArrow from '../../assets/up-arrow.svg'
import downArrow from '../../assets/down-arrow.svg'
import './Chart.css';
import Axios from 'axios';
import dayjs from 'dayjs';
import Chart from '../ChartWrapper/ChartWrapper';
import 'bootstrap/dist/css/bootstrap.min.css'

export default class ChartComponent extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            data: null,
            signals: [],
            interval: null,
            chartId: this.props.match.params.id
        }

        this.getData = this.getData.bind(this);
        this.getSignals = this.getSignals.bind(this);
    }


    getSignals(){
        Axios.get(`${API_URL}/api/signals?market=${this.state.chartId}`)
        .then(data => data.data).then((data) => {
            console.log(data)
            this.setState({
                signals: data
            })
        })
    }

    getData(){
        fetch(`${API_URL}/api/market?market=${this.state.chartId}`).then(response => response.json()).then((data) => {
            // console.log(data)
			data.forEach((d) => {
				d.time = new Date(d.time * 1000)
			})
			data.sort((a, b) => {
				return a.time - b.time;
			})
			this.setState({
				data: data
            })
            console.log(data)
		})
    }

	componentDidMount() {
        this.getData();
        this.getSignals();
        // this.socket = io.connect(`${SOCKET_URL}`, {
        //     reconnection: true,
        //     reconnectionDelay: 500,
        //     reconnectionAttempts: 10
        // });
        // this.socket.emit('start-socket', this.props.match.params.id);
        // this.socket.on('buy-signal', (data) => {
        //     data.type = 'buy'
        //     this.setState((prevState) => ({
        //         signals: [data, ...prevState.signals]
        //     }))
        // })

        // this.socket.on('sell-signal', (data) => {
        //     data.type = 'sell'
        //     this.setState((prevState) => ({
        //         signals: [data, ...prevState.signals]
        //     }))
        // })
        // this.interval = setInterval(() => {
        //     Axios.get(`${API_URL}/signals`).then((data) => {
        //         this.setState({
        //             signals: data.data
        //         })
        //     })
        // }, 10000);
        this.setState({
            interval: setInterval(() => {
                this.getData();
                this.getSignals();
            }, 10000)
        })
		
    }
    
    componentWillUnmount(){
        clearInterval(this.state.interval)
    }

	render() {
		if (this.state.data === null) {
			return <div>Loading...</div>
		}
		return (
            <React.Fragment>
                <div className="chart">
                    <Chart data={this.state.data} />
                </div>
                {/* <div className="notifications"> */}
                <div className="table-height table-responsive">
                    <table className="table" style={{
                        width: "900px",
                        margin: "10px auto",
                        overflow: 'scroll'
                    }}>
                    <colgroup>
                        <col span="1" style={{width: "5%"}} />
                        <col span="1" style={{width: "40%"}} />
                        <col span="1" style={{width: "5%"}} />
                        <col span="1" style={{width: "25%"}} />
                        <col span="1" style={{width: "25%"}} />
                    </colgroup>
                
                        <thead className="thead-light">
                            <tr>
                                <th>Buy/Sell</th>
                                <th>Date</th>
                                <th>Entry</th>
                                <th>Take Profit</th>
                                <th>Stop Loss</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.signals.map((signal) => {
                                    return (
                                    <tr>
                                        <td>
                                            {signal.type == 'buy' ? <React.Fragment><img src={upArrow} /> Buy</React.Fragment> : <React.Fragment><img src={downArrow} /> Sell</React.Fragment>}
                                        </td>
                                        <td>{dayjs(signal.time * 1000).format("DD-MM-YYYY HH:MM")}</td>
                                        <td>{signal.close}</td>
                                        <td>{signal[`${signal.type}_take_profit`]}</td>
                                        <td>{signal[`${signal.type}_stop_loss`]}</td>
                                    </tr>
                                    )
                                })
                            }
                            
                        </tbody>
                    </table>
                </div>
            {/* </div> */}
        </React.Fragment>
		)
	}
}
